import "./App.css";
import React from "react";
import { useEffect } from "react";
import { LogIn } from "./views/login";
import AOS from "aos";
import "aos/dist/aos.css";

function App() {
  useEffect(() => {
    AOS.init();
    AOS.refresh();
  }, []);
  return (
    <div className="App">
      <LogIn />
    </div>
  );
}

export default App;
