import React from "react";
import Box from "@mui/material/Box";
import Drawer from "@mui/material/Drawer";
import { Grid } from "@mui/material";
import Button from "@mui/material/Button";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import { ThemeProvider, createTheme } from "@mui/material/styles";
import MenuIcon from "@mui/icons-material/Menu";
import EmailIcon from "@mui/icons-material/Email";
import GitHubIcon from "@mui/icons-material/GitHub";
import LinkedInIcon from "@mui/icons-material/LinkedIn";

const darkTheme = createTheme({
  palette: {
    mode: "dark",
    primary: {
      main: "#f96d00",
    },
  },
});

export class NavB extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      open: false,
      lenguaje: this.props.lenguaje,
    };
    this.goToGit = this.goToGit.bind(this);
    this.goToLink = this.goToLink.bind(this);
    this.goToMail = this.goToMail.bind(this);
  }

  toggleDrawer(valor) {
    this.setState({ open: valor });
  }

  goToGit() {
    window.location.href = "https://github.com/av-edson";
  }
  goToLink() {
    window.location.href = "https://www.linkedin.com/in/edson-avila-725227212/";
  }
  goToMail() {
    window.location.href = "mailto:esavilaortiz@gmail.com";
  }

  render() {
    return (
      <ThemeProvider theme={darkTheme}>
        <Grid
          container
          justifyContent="flex-end"
          sx={{ width: "100px", backgroundColor: "#0F0E0E" }}
        >
          <Grid item>
            <Button
              onClick={() => this.toggleDrawer(true)}
              endIcon={<MenuIcon color="primary" />}
            ></Button>
          </Grid>
          <Drawer
            anchor="left"
            open={this.state.open}
            onClose={() => this.toggleDrawer(false)}
          >
            <Box
              role="presentation"
              onClick={() => this.toggleDrawer(false)}
              onKeyDown={() => this.toggleDrawer(false)}
              sx={{ backgroundColor: "#0F0E0E", height: "100%" }}
            >
              <List>
                <ListItem button onClick={this.goToGit}>
                  <ListItemIcon>
                    <GitHubIcon color="primary" />
                  </ListItemIcon>
                  <ListItemText primary="GitHub" />
                </ListItem>
                <ListItem button onClick={this.goToLink}>
                  <ListItemIcon>
                    <LinkedInIcon color="primary" />
                  </ListItemIcon>
                  <ListItemText primary="Linkedin" />
                </ListItem>
                <ListItem button onClick={this.goToMail}>
                  <ListItemIcon>
                    <EmailIcon color="primary" />
                  </ListItemIcon>
                  {this.props.lenguaje === 1 ? (
                    <ListItemText primary="Contact Me" />
                  ) : (
                    <ListItemText primary="Contacta Me" />
                  )}
                </ListItem>
              </List>
            </Box>
          </Drawer>
        </Grid>
      </ThemeProvider>
    );
  }
}
