import React from "react";
import Grid from "@mui/material/Grid";
import Tooltip from "@mui/material/Tooltip";
import Dialog from "@mui/material/Dialog";
import DialogContent from "@mui/material/DialogContent";
import DialogTitle from "@mui/material/DialogTitle";
import IconButton from "@mui/material/IconButton";
import CloseIcon from "@mui/icons-material/Close";

export class Sckill extends React.Component {
  state = {
    url: "",
    name: "",
    content: "",
    view: false,
    id: 0,
    onClose: false,
  };

  componentDidMount = () => {
    var data = this.props.data;
    this.setState({
      url: data.url,
      name: data.name,
      id: data.id,
      content: data.content,
      content2: data.content2,
    });
  };

  handleOpen() {
    this.setState({ view: true });
  }
  handleClose() {
    this.setState({ view: false });
  }

  render() {
    return (
      <Grid item xs={3} md={2} sx={{ marginLeft: "40px" }} className="icon">
        <Tooltip title={this.state.name} onClick={() => this.handleOpen()}>
          <img alt="" src={this.state.url} width="70%" data-aos="flip-up" />
        </Tooltip>
        <Dialog open={this.state.view} onClose={() => this.handleClose()}>
          <DialogTitle
            id={this.state.url}
            sx={{ backgroundColor: "black" }}
            className="texto"
          >
            {this.props.lenguaje === 1 ? (
              <div>{"What is " + this.state.name + " ?"}</div>
            ) : (
              <div>{"Qué es " + this.state.name + " ?"}</div>
            )}
            <IconButton
              aria-label="close"
              onClick={() => this.handleClose()}
              sx={{
                position: "absolute",
                right: 8,
                top: 8,
                color: (theme) => theme.palette.grey[500],
              }}
            >
              <CloseIcon />
            </IconButton>
          </DialogTitle>
          <DialogContent sx={{ backgroundColor: "black" }}>
            {this.props.lenguaje === 1 ? (
              <p id={this.state.name} className="texto2">
                {this.state.content}
              </p>
            ) : (
              <p id={this.state.name} className="texto2">
                {this.state.content2}
              </p>
            )}
          </DialogContent>
        </Dialog>
      </Grid>
    );
  }
}
