import React from "react";
import AppBar from "@mui/material/AppBar";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import { NavB } from "./navBar";
import FadeMenu from "./translate";
import { Grid } from "@mui/material";

const theme = createTheme({
  typography: {
    fontFamily: "Nunito",
  },
  palette: {
    primary: {
      main: "#7A0BC0",
    },
    secondary: {
      main: "#f96d00",
    },
  },
});
export class Appbar extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      open: false,
      lenguaje: 1,
    };
    this.changeLenguaje = this.changeLenguaje.bind(this);
  }

  changeLenguaje(nuevo) {
    this.setState({ lenguaje: nuevo });
    this.props.callFunction(nuevo);
  }
  render() {
    return (
      <ThemeProvider theme={theme}>
        <AppBar
          sx={{
            backgroundColor: "#0F0E0E",
            width: "100%",
          }}
        >
          <Toolbar>
            <Typography
              variant="h6"
              sx={{ flexGrow: 0, marginRight: 1 }}
              className="texto"
            >
              {this.state.lenguaje === 1 ? "Edson's" : "Portafolio de"}
            </Typography>
            <Typography variant="h6" sx={{ flexGrow: 1 }} className="texto">
              {this.state.lenguaje === 1 ? "Portafolio" : "Edson"}
            </Typography>
            <Grid width={"30%"}>
              <FadeMenu callFunction={this.changeLenguaje} />
            </Grid>
            <NavB lenguaje={this.state.lenguaje} />
          </Toolbar>
        </AppBar>
      </ThemeProvider>
    );
  }
}
