import React from "react";
import { Component } from "react";
import Button from "@mui/material/Button";
import TranslateIcon from "@mui/icons-material/Translate";
import Menu from "@mui/material/Menu";
import MenuItem from "@mui/material/MenuItem";
import Fade from "@mui/material/Fade";
import { Grid, Typography } from "@mui/material";

class FadeMenu extends Component {
  constructor(props) {
    super(props);
    this.state = {
      anchorEl: null,
      open: false,
      texto: "English",
    };
    this.setAnchorEl = this.setAnchorEl.bind(this);
    this.handleClick = this.handleClick.bind(this);
    this.handleClose = this.handleClose.bind(this);
    this.handleEnglish = this.handleEnglish.bind(this);
    this.handleSpanish = this.handleSpanish.bind(this);
  }
  handleClick(event) {
    this.setAnchorEl(event.currentTarget);
  }
  setAnchorEl(value) {
    this.setState({
      anchorEl: value,
      open: !this.state.open,
    });
  }
  handleClose() {
    this.setAnchorEl(null);
  }
  handleSpanish() {
    this.handleClose();
    this.setState({ texto: "Español" });
    this.props.callFunction(2);
  }
  handleEnglish() {
    this.handleClose();
    this.setState({ texto: "English" });
    this.props.callFunction(1);
  }
  renderMenu() {
    return (
      <Menu
        id="fade-menu"
        anchorEl={this.state.anchorEl}
        open={this.state.open}
        onClose={this.handleClose}
        TransitionComponent={Fade}
      >
        <MenuItem onClick={this.handleSpanish}>Spanish</MenuItem>
        <MenuItem onClick={this.handleEnglish}>English</MenuItem>
      </Menu>
    );
  }
  render() {
    return (
      <div>
        <Grid container justifyContent="flex-end">
          <Grid item xs={2}>
            <Button
              aria-owns={this.state.open ? "fade-menu" : undefined}
              aria-haspopup="true"
              onClick={this.handleClick}
            >
              <TranslateIcon color="secondary" />
            </Button>
          </Grid>
          <Grid item xs={2} className="ocultar" sx={{ marginTop: "5px" }}>
            <Typography className="texto">{this.state.texto}</Typography>
          </Grid>
        </Grid>
        {this.renderMenu()}
      </div>
    );
  }
}

export default FadeMenu;
